.advantages--bg {
  padding-top: 40px;
  padding-bottom: 40px;
  background: #fff5f6;
}

@media screen and (max-width: 767px) {
  .advantages__item:not(:last-child) {
    margin-bottom: 32px;
    // background-color: red;
  }
}

@media screen and (min-width: 768px) {
  .advantages__list {
    display: flex;
  }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .advantages__item:nth-child(1) {
    // background-color: orange;
    margin-right: 4px;
  }
  .advantages__item:nth-child(2) {
    // background-color: orange;
    margin-right: 22px;
  }
}

@media screen and (min-width: 1200px) {
  .advantages__item:nth-child(1) {
    // background-color: lawngreen;
    margin-right: 115px;
  }
  .advantages__item:nth-child(2) {
    // background-color: lawngreen;
    margin-right: 80px;
  }
}

.advantages_text-box::before {
  content: '';
  display: block;

  @media screen and (max-width: 767px) {
    margin-bottom: 16px;
  }

  @media screen and (min-width: 768px) and (max-width: 1199px) {
    margin-bottom: 36px;
  }

  @media screen and (min-width: 1200px) {
    margin-bottom: 40px;
  }
}

.advantages_text-box--1::before {
  background: url('../images/img-adv/1.png') no-repeat;
  background-size: cover;

  width: 33px;
  height: 50px;
}

@media (min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
  .advantages_text-box--1::before {
    background-image: url('../images/img-adv/1@2x.png');
  }
}

.advantages_text-box--2::before {
  background: url('../images/img-adv/2.png') no-repeat;
  background-size: cover;

  width: 50px;
  height: 50px;
}

@media (min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
  .advantages_text-box--2::before {
    background-image: url('../images/img-adv/2@2x.png');
  }
}

.advantages_text-box--3::before {
  background: url('../images/img-adv/3.png') no-repeat;
  background-size: cover;

  width: 47px;
  height: 50px;
}

@media (min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
  .advantages_text-box--3::before {
    background-image: url('../images/img-adv/3@2x.png');
  }
}

.advantages__title {
  font-family: 'Titan One';
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 41px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #d41443;

  @media screen and (max-width: 767px) {
    margin-bottom: 16px;
  }

  @media screen and (min-width: 768px) and (max-width: 1199px) {
    margin-bottom: 13px;
  }

  @media screen and (min-width: 1200px) {
    margin-bottom: 16px;
  }
}

.advantages__text {
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 700;

  color: #000000;

  @media screen and (max-width: 767px) {
    width: 230px;

    font-size: 12px;
    line-height: 170%;
  }

  @media screen and (min-width: 768px) and (max-width: 1199px) {
    width: 180px;

    font-size: 14px;
    line-height: 174%;
  }

  @media screen and (min-width: 1200px) {
    width: 288px;

    font-size: 16px;
    line-height: 176%;

    letter-spacing: 0.02em;
  }
}

.visually-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}
