.header {
    margin-left: auto;
    margin-right: auto;
    position: relative;
    
}

.header__container {
    
    padding-top: 8px;
    background-color: #FFB8CA;
    position: relative;
    height: 502px;
    overflow: hidden;
    
    @include mq(tablet) {
           padding-top: 32px;
           height: 416px;
        }

    @include mq(desctop) {
        padding-top: 37px;
        height: 664px;
    }
}

.header__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;


    position: relative;

    @include mq(tablet) {
       justify-content: space-between;
    }
}

.header__tablet-wrap {
    display: flex;
}



// =============================
//  - 
// =============================

.header__menu-button {
    cursor: pointer;
    background-color: transparent;
    border: none;
    margin: 0;
    padding: 0;
    z-index: 101;



  @include mq(tablet-only) {
    top: 18px;
    right: 192px;
    position: absolute;
}

    @include mq (desctop) {
            display: none;
        }
}


.header__menu-button.is-open  {
    display: block;
    @include mq(tablet-only) {
            top: -12px;
            right: 0;
            position: absolute;
        }

    @include mq(desctop) {
        display: none;
     }

}

.header__menu-button .header__icon-close {
    display: none;
}

.header__menu-button.is-open .header__icon-close {
    display: block;

}

.header__menu-button.is-open .header__icon-burger {
    display: none;
}

//  "By-now'   ( + )

.header__menu-btn {
    
    display: block;
    width: 160px;
    height: 44px;
    background: #FFFFFF;
    border-radius: 22px;
    border: none;
    cursor: pointer;
    animation: heartbeat 1.5s ease-in-out infinite both;
    
    font-weight: 700;
    font-size: 16px;
    line-height: 1.3;
    color: #D41443;

    @include mq(mobile-only) {
        display: none;
    }

    @include mq(tablet-only) { 
    margin-left: 32px;
    }


    &::after {
        content: '';
        background-image: url(../images/header-hero/mob-tab_menu/arrow_mob-tab-menu.svg);
        background-repeat: no-repeat;
         width: 8px;
         height: 10px;
        display: inline-flex;
        margin-left: 12px;
    }
        
}


// ===================
//  
// ===================

.mob-menu {
    top: 0;
    right: 0;
    background-color: #E17992;
    position: absolute;
    width: 255px;
    height: 502px;
    transform: translateX(100%);
    transition: transform 300ms linear;
    z-index: 100;


    &.is-open {
        transform: translateX(0);
    }

 @include mq(tablet) {
    height: 416px;
 }

 @include mq(desctop) {
     display: none;
 }
}





