// ORIGIN

.mob-menu__btn-purchase {
    display: block;
    margin-top: 60px;
    width: 209px;
    height: 44px;
    background: #FFFFFF;
    border-radius: 22px;
    border: none;
    margin-left: auto;
    margin-right: auto;
    cursor: pointer;
    margin-bottom: 19px;

    font-weight: 700;
