.header__go-to-top {
    position: fixed;
    bottom: 20px;
    right: 10px;
    display: block;
    width: 35px;
    height: 35px;
    z-index: 99;
    cursor: pointer;
    transform: rotate(0);
    transition: transform 150ms linear;
    

    @include mq(tablet) {
            width: 40px;
            height: 40px;
        }


    @include mq(desctop) {
        width: 50px;
        height: 50px;
    }   
}


.header__go-to-top:hover,
.header__go-to-top:focus {
    filter: brightness(1.1);
    outline: none;
    transform: rotate(-45deg)
}


.header__go-to-top::after {
    position: absolute;
    display: block;
    content: '';
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 30px;
    height: 30px;
    background-image: url(../images/header-hero/ice-cream-scroll.png);
    background-size: contain;
    background-repeat: no-repeat;

    @include mq(tablet) {
            width: 35px;
            height: 35px;
        }
    

    @include mq(desctop) {
            width: 45px;
            height: 45px;
        }
     
}







